import { createMovieCard } from './get-trend-movies';
import { FetchMoviesApi } from './get-trend-movies';
import { pagination } from './pagination';
import Pagination from 'tui-pagination';

const fetchMoviesApi = new FetchMoviesApi();

window.addEventListener('load', uploadTrendMovies);

async function uploadTrendMovies() {
//   await DataFetch.fetchGenres();
  await fetchMoviesApi.fetchTrendFilms().then(films => {
      createMovieCard(films);
    
  
    //   pagination();
  });
  pagination();

}